.reviews {
    padding-bottom: 80px;
    @media (min-width:@desktop-size) {
        padding-bottom: 235px;
    }
}

.reviews__title {
    .font-style(24px; 29px; 700);
    text-align: center;
    margin: 0;
    padding-bottom: 8px;
    @media (min-width:@desktop-size) {
        .font-style(48px; 58px; 700);
        padding-bottom: 16px;
    }
}

.reviews__text {
    .font-style(15px; 23px; 400);
    text-align: center;
    margin: 0;
    padding: 0 35px 20px;
    @media (min-width:@desktop-size) {
        .font-style(18px; 27px; 400);
        padding-bottom: 40px;
    }
}

.reviews__list {
    list-style: none;
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
    display: flex; 
    flex-direction: row;
    gap: 12px;
    overflow-x: scroll;
    @media (min-width:@desktop-size) {
        gap: 20px;
    }
}

.reviews__item {
    border: 1px solid @grey;
    border-radius: 12px;
    min-width: 280px;
    padding-top: 20px;
    @media (min-width:@desktop-size) {
        max-width: 427px;
    }
    
}

.reviews__item-title {
    .font-style(16px; 22px; 400);
    color: @turquoise;
    margin: 0;
    margin-left: 16px;
    margin-right: 16px;
    padding-bottom: 4px;
}

.reviews__item-text {
    .font-style(14px; 20px; 400);
    margin: 0;
    margin-left: 16px;
    margin-right: 16px;
    margin-bottom: 8px;
    height: 164px;
	overflow: hidden;
    text-overflow: ellipsis;
}

.reviews__item-who {
    .font-style(14px; 20px; 500);
    color: @grey-turquoise;
    margin: 0;
    margin-left: 16px;
    margin-right: 16px;
    padding-bottom: 16px;
    &--name {
        color: @special-black;
    }
}

.reviews__item-info {
    background-color: @light-grey;
    border-radius: 12px;
    bottom: 0;
    left: 0;
    padding-left: 16px;
    padding-right: 16px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;
}

.reviews__item-data {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;
    gap: 26px;
}

.reviews__item-whom-name {
    position: relative;
    &::after {
        position: absolute;
        content: "";
        width: 2px;
        height: 16px;
        border-radius: 1px;
        top: 2px;
        right: -12px;
        background-color: @grey;
    }
}

.reviews__item-stars {
    .font-style(18px; 27px; 400);
    color: @turquoise;
    position: relative;
    &::after {
        position: absolute;
        content: "";
        width: 17px;
        height: 16px;
        top: 4px;
        left: 15px;
        background-image: url("../img/star-icon.svg");
    }
}

.reviews__item-resource {
    color: @special-black;
    text-decoration: none;
    position: relative;
    // margin-right: 10px;
    &--name {
        display: none;
        @media (min-width: @desktop-size) {
            display: block;
        }
        // position: relative;
        // margin-right: 10px;
    }
    &--arrow {
        fill: @special-black;
    }
    &--yandex::before {
        // @media (max-width: @mobil-size) {
            position: absolute;
            content: "";
            width: 20px;
            height: 20px;
            top: -2px;
            left: -30px;
            background-image: url("../img/yandex-icon.png");
        // }
        @media (min-width: @desktop-size) {
            display: none;
        }
    }
}

.reviews__filter-button {
    display: none;
}
