.news {
    padding-bottom: 80px;
    @media (min-width:@desktop-size) {
        padding-bottom: 120px;
    }
}

.news-wrapper {
    @media (min-width:@desktop-size) {
        position: relative;
    }
}

.news__title {
    .font-style(24px; 29px; 700);
    text-align: center;
    margin: 0;
    padding-bottom: 8px;
    @media (min-width:@desktop-size) {
        .font-style(48px; 58px; 700);
        padding-bottom: 16px;
    }
}

.news__text {
    .font-style(15px; 23px; 400);
    text-align: center;
    margin: 0;
    padding-bottom: 16px;
    @media (min-width:@desktop-size) {
        .font-style(18px; 27px; 400);
        padding-bottom: 40px;
    }
} 

.news__options-type-list {
    list-style: none;
    padding: 4px;
    margin: 0;
    margin-bottom: 16px;
    border: 1px solid @grey;
    border-radius: 8px;
    display: flex;
    justify-content: space-between;
    @media (min-width:@desktop-size) {
        width: max-content;
        position: absolute;
        top: 28px;
        right: 0;
    }
}

.news__options-type-item {
    .font-style(12px; 14px; 400);
    padding: 11px 18px;
    text-align: center;
    flex-grow: 1;
    &--current {
        background-color: @grey;
        border-radius: 6px;
    }
}

.news__list {
    list-style: none;
    padding: 0;
    margin: 0;
    margin-bottom: 20px;
    display: flex; 
    flex-direction: row;
    gap: 12px;
    overflow-x: scroll;
    @media (min-width:@desktop-size) {
        margin-bottom: 32px;
    }
}

.news__item {
    background-color: @light-grey;
    border-radius: 12px;
    min-width: 280px;
    padding-top: 8px;
    padding-bottom: 14px;
    padding-left: 8px;
    padding-right: 8px;
    position: relative;
    @media (min-width:@desktop-size) {
        max-width: 427px;
    }
}

.news__item-image { 
    width: 100%;
    height: 240px;
    object-fit: cover;
    border-radius: 24px;
    margin-bottom: 20px;
}

.news__item-title {
    .font-style(16px; 22px; 400);
    margin: 0 16px;
    padding-bottom: 4px;
    @media (min-width:@desktop-size) {
        .font-style(18px; 27px; 400);
        padding-bottom: 8px;
    }
}

.news__item-text {
    .font-style(14px; 21px; 400);
    margin: 0 16px;
    height: 64px;
    overflow: hidden;
    text-overflow: ellipsis;
    @media (min-width:@desktop-size) {
        .font-style(15px; 23px; 400);
    }
}

.news__item-time {
    .font-style(12px; 18px; 400);
    background-color: @white;
    padding: 6px 10px;
    border-radius: 6px;
    position: absolute;
    top: 16px;
    left: 16px;
    @media (min-width:@desktop-size) {
        // .font-style(15px; 23px; 400);
    }
}

.news__filter-button {
    display: none;
}
