.reasons {
    margin: 0;
    padding-bottom: 40px;
}

.reasons__title {
    .font-style(16px; 24px; 400);
    @media (min-width:@desktop-size) {
        .font-style(18px; 27px; 400);
    }
}

.reasons__list {
    list-style: none;
    padding: 0;
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.reasons__item {
    display: flex;
    flex-direction: column;
    gap: 12px;
    padding: 16px 0;
    border-bottom: 1px solid @grey;
    &:last-child {
        border: none;
    }
    @media (min-width:@desktop-size) {
        flex-direction: row;
        gap: 86px;
        flex-basis: content;
        // justify-content: space-between;
    }
}

.reasons__item-title {
    .font-style(24px; 29px; 700);
    margin: 0;
    @media (min-width:@desktop-size) {
        .font-style(28px; 34px; 700);
        width: 50%;
        padding-left: 70px;
    }
}

.reasons__item-text {
    .font-style(14px; 21px; 400);
    margin: 0;
    @media (min-width:@desktop-size) {
        .font-style(15px; 23px; 400);
        width: 50%;
    }
}
