.possibilities {
    padding: 80px 0;
    @media (min-width:@desktop-size) {
        padding: 120px 0;
    }
}

.possibilities-title {
    .font-style(24px; 29px; 700);
    text-align: center;
    margin: 0;
    padding-bottom: 8px;
    @media (min-width:@desktop-size) {
        .font-style(48px; 58px; 700);
        padding-bottom: 16px;
    }
}

.possibilities-text {
    .font-style(15px; 23px; 400);
    text-align: center;
    margin: 0;
    padding: 0 35px 20px;
    @media (min-width:@desktop-size) {
        .font-style(18px; 27px; 400);
        padding-bottom: 40px;
    }
}

.possibilities-block {
    border: 1px solid @grey;
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    @media (min-width:@desktop-size) {
        flex-direction: row;
    }
}

.possibilities-list {
    border-bottom: 1px solid @grey;
    border-radius: 12px;
    padding: 16px;
    @media (min-width:@desktop-size) {
        padding: 24px;
        border-right: 1px solid @grey;
        width: 315px;
        flex-shrink: 0;
    }
}

.possibilities-list__title {
    .font-style(18px; 27px; 400);
    margin: 0;
    padding-bottom: 12px;
}

.possibilities-list__list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 8px;
}

.possibilities-list__item {
    .font-style(14px; 21px; 400);
    text-align: center;
    padding: 12px 0;
    background-color: @light-grey;
    border-radius: 6px;
    position: relative;
    &--current {
        background-color: @light-turquoise;
        padding-left: 20px;
        &::after {
            position: absolute;
            content: "";
            width: 12px;
            height: 12px;
            border-radius: 12px;
            background-color: @turquoise;
            top: 16px;
            left: 56px;
        }
    }
    @media (min-width:@desktop-size) {
        .font-style(15px; 23px; 400);
    }
}

.possibilities__promo {
    padding: 24px 16px;
    // @media (min-width:@desktop-size) {
    //     width: auto;
    // }
}

.possibilities__promo-title {
    .font-style(16px; 22px; 400);
    margin: 0;
    padding: 0;
    padding-bottom: 8px;
    @media (min-width:@desktop-size) {
        .font-style(18px; 27px; 400);
    }
}

.possibilities__promo-text {
    .font-style(14px; 21px; 400);
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    gap: 12px;
    @media (min-width:@desktop-size) {
        .font-style(15px; 23px; 400);
    }
}

.possibilities__promo-text-elem {
    margin: 0;
}

.possibilities__promo-text-list {
    list-style: none;
    padding-left: 20px;
    li {
        padding-bottom: 8px;
        position: relative;
        &::before {
            position: absolute;
            content: "";
            background-image: url("../img/list-point-icon.svg");
            width: 12px;
            height: 10px;
            top: 5px;
            left: -20px;
        }
        &:last-child {
            padding-bottom: 0;
        }
    }
}

.possibilities__promo-image {
    display: none;
    @media (min-width:@desktop-size) {
        display: block;
        width: 258px;
        height: auto;
        object-fit: cover;
        border-radius: 8px;
        margin: 8px;
    }
}
