@font-face {
  font-family: "Stolzl";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src:
    url("../fonts/Stolzl-Regular.woff2") format("woff2"),
    url("../fonts/Stolzl-Regular.woff") format("woff");
}

@font-face {
  font-family: "Stolzl";
  font-weight: 500;
  font-style: medium;
  font-display: swap;
  src:
    url("../fonts/Stolzl-Medium.woff2") format("woff2"),
    url("../fonts/Stolzl-Medium.woff") format("woff");
}

@font-face {
  font-family: "Stolzl";
  font-weight: bold;
  font-style: bold;
  font-display: swap;
  src:
    url("../fonts/Stolzl-Bold.woff2") format("woff2"),
    url("../fonts/Stolzl-Bold.woff") format("woff");
}
