.welcome {
    padding-top: 116px;
    padding-bottom: 80px;
}

.welcome-wrapper {
    display: flex;
    flex-direction: column;
    gap: 12px;
    @media (min-width:@desktop-size) {
        flex-direction: row-reverse;
        gap: 20px;
    }
}

// .welcome__image-container {
//     padding: 0;
//     width: 320px;
//     height: 280px;
//     border-radius: 16px;
// }

.welcome__image {
    object-fit: cover;
    width: 100%;
    min-height: 280px;
    height: auto;
    border-radius: 16px;
    @media (min-width:@desktop-size) {
        width: 50%;
    }
}

.welcome__speech {
    background-image: url("../img/bg-print.svg");
    background-repeat: no-repeat;
    background-position: 80% 97%;
    background-color: @light-grey;
    border-radius: 16px;
    padding-top: 16px;
    padding-bottom: 24px;
    padding-left: 16px;
    padding-right: 16px;
}

.welcome__speech-title {
    .font-style(20px; 24px; 700);
    margin: 0;
    padding-bottom: 12px;
    .title--color {
        color: @turquoise;
    }
}

.welcome__speech-text {
    .font-style(14px; 21px; 400);
    margin: 0;
    padding-bottom: 20px;
}
