.standart-button {
    .font-style(15px; 20px; 400);
    background-color: @turquoise;
    color: @white;
    border: none;
    border-radius: 8px;
    padding: 10px 24px;
    width: 100%;
    height: min-content;
    @media (min-width:@desktop-size) {
        .font-style(15px; 23px; 400);
        padding-top: 12px;
        padding-bottom: 14px;
        width: max-content;
    }
}

.appointment__form-button {
    flex-shrink: 0;
}

.specialist__appointment-button {
    .font-style(14px; 21px; 400);
    background-color: @grey;
    color: @black;
    border: none;
    border-radius: 8px;
    padding: 10px 24px;
    width: 100%;
    &--price {
        .font-style(14px; 21px; 500);
    }
    @media (min-width:@desktop-size) {
        .font-style(15px; 23px; 400);
        &--price {
            .font-style(15px; 23px; 500);
        }
    }
}

.specialist__button {
    @media (min-width:@desktop-size) {
        margin: 0 auto;
        display: block;
    }
}

.news__button {
    @media (min-width:@desktop-size) {
        margin: 0 auto;
        display: block;
    }
}

.footer-section__button-standart {
    .font-style(14px; 21px; 400);
    width: 100%;
    padding: 10px 24px;
    border: 1px solid @white;
    border-radius: 8px;
    &--question {
        color: @special-black;
        background-color: @white;
    }
    &--special {
        color: @white;
        background-color: transparent;
        &-arrow {
            width: 12px;
            height: 12px;
            padding-left: 15px;
            // fill: @white;
            fill: greenyellow;
            stroke: greenyellow;
        }
    }
    @media (min-width:@desktop-size) {
        .font-style(15px; 23px; 400);
    }
}