.blockquote {
    padding-bottom: 80px;
    @media (min-width:@desktop-size) {
        padding-bottom: 120px;
    }
}

.blockquote__preface {
    .font-style(15px; 23px; 400);
    margin: 0;
    padding-bottom: 12px;
    @media (min-width:@desktop-size) {
        .font-style(18px; 27px; 400);
        padding-bottom: 28px;
    }
}

.blockquote__big-quot {
    .font-style(20px; 26px; 700);
    margin: 0;
    padding-bottom: 8px;
    .title--color {
        color: @turquoise;
    }
    @media (min-width:@desktop-size) {
        .font-style(80px; 80px; 700);
        padding-bottom: 60px;
    }
}

.blockquote__container {
    @media (min-width:@desktop-size) {
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
        gap: 100px;
    }
}


.blockquote__small-quot {
    .font-style(14px; 20px; 400);
    margin: 0;
    padding-bottom: 16px;
    @media (min-width:@desktop-size) {
        .font-style(15px; 24px; 400);
        padding: 0;
        padding-left: 90px;
        max-width: 650px;
        position: relative;
        &::before {
            position: absolute;
            content: "";
            height: 60px;
            width: 67px;
            top: 0;
            left: 0;
            background-image: url("../img/blockquote-icon.svg");
            background-repeat: no-repeat;
        }
    }
}

.blockquote__author {
    display: flex;
    flex-direction: row;
    align-content: flex-start;
    gap: 12px;
    @media (min-width:@desktop-size) {
        min-width: 400px;
        gap: 20px;
    }
}

.blockquote__author-image {
    @media (min-width:@desktop-size) {
        width: 100px;
        height: 100px;
    }
}

.blockquote__author-info {
    margin: 6px 0;
    @media (min-width:@desktop-size) {
        margin: 22px 0;
        height: max-content;
    }
}

.blockquote__author-name {
    .font-style(15px; 23px; 400);
    margin: 0;
    @media (min-width:@desktop-size) {
        .font-style(18px; 27px; 400);
    }
}

.blockquote__author-status {
    .font-style(14px; 20px; 400);
    margin: 0;
    @media (min-width:@desktop-size) {
        .font-style(15px; 21px; 400);
    }
}
