.partners {
    padding-bottom: 80px;
}

.partners__title {
    .font-style(24px; 29px; 700);
    text-align: center;
    margin: 0;
    padding-bottom: 8px;
}

.partners__text {
    .font-style(15px; 23px; 400);
    text-align: center;
    margin: 0;
    padding-bottom: 18px;
}

.partners__list {
    list-style: none;
    padding: 0;
    margin: 0;
    overflow-x: scroll;
    display: flex;
    flex-direction: row;
    gap: 12px;
}

.partners__item {
    // display: block;
    height: 80px;
    width: 157px;
    border-radius: 10px;
    background-color: @light-grey;
    display: flex;
    align-content: center;
    justify-content: center;
}

.partners__item-logo {
    width: 156px;
    height: 80px;
    object-fit: contain;
}
