.page__body {
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  font-family: "Stolzl", sans-serif;
  color: #222222;
  font-size: 14px;
  font-weight: normal;
  line-height: 21px;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.wrapper {
  width: auto;
  margin: 0 20px;
  overflow: visible;
}
@media (min-width: 768px) {
  .wrapper {
    margin: 0 60px;
  }
}
@media (min-width: 1440px) {
  .wrapper {
    width: 1320px;
    margin: 0 auto;
  }
}
