.main-nav {
  display: none;
  width: 100%;
}
@media (min-width: 1200px) {
  .main-nav {
    display: block;
    padding: 0;
    margin: 0;
  }
}
@media (min-width: 1200px) {
  .main-nav__wrapper {
    display: grid;
    grid-template-columns: 1fr auto;
    gap: 50px;
  }
}
.main-nav__toggle {
  position: absolute;
  top: 0;
  left: 0;
  width: 44px;
  height: 44px;
  background-color: transparent;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}
.main-nav__toggle::before,
.main-nav__toggle::after {
  position: absolute;
  content: "";
  width: 16px;
  height: 1px;
  left: 14px;
  border-radius: 1px;
  background-color: #222222;
}
.main-nav__toggle::before {
  top: 16px;
}
.main-nav__toggle::after {
  bottom: 16px;
}
@media (min-width: 1200px) {
  .main-nav__toggle {
    display: none;
  }
}
