.intro {
    background-image: url("../img/anastasia-romanova-mobil.png"), 
                      url("../img/irina-vasilieva-mobil.png"), 
                      url("../img/alina-kirillova-mobil.png"), 
                      url("../img/maria-pozharskaya-mobil.png"), 
                      url("../img/vitali-petrov-mobil.png");
    background-repeat: no-repeat;
    background-position:0 0, 25% 21px, 50% 10px, 75% 0, 100% 0;
    padding-top: 130px;
    padding-bottom: 80px;
    position: relative;
    // box-sizing: border-box;
    &::before {
        position: absolute;
        content: "";
        width: 100vw;
        height: 100px;
        top: 100px;
        left: 0;
        background: linear-gradient(360deg, #F5F6F6 45%, rgba(245, 246, 246, 0) 100%);
    }
    @media (min-width:@desktop-size) {
        padding-top: 56px;
        padding-bottom: 220px;
        // visibility: visible;
    }
}

.intro__wrapper {
    position: relative;
}

.intro__promo {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.intro__title {
    .font-style(30px; 33px; 700);
    text-align: center;
    margin: 0;
    margin-bottom: 10px;
    .title--color {
        color: @turquoise;
    }
    @media (min-width:@desktop-size) {
        .font-style(88px; 96px; 700);
        margin-bottom: 24px;
    }
}

.intro__text {
    .font-style(16px; 24px; 400);
    text-align: center;
    margin: 0;
    margin-bottom: 18px;
    @media (min-width:@desktop-size) {
        .font-style(18px; 27px; 400);
        margin-bottom: 32px;
        max-width: 655px;

    }
}

.intro__advantage {
    position: absolute;
    bottom: -120px;
    left: 0;
    right: 0;
    background-color: @white;
    border: 1px solid @grey;
    border-radius: 12px;
    padding: 16px 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
    z-index: 5;
    @media (min-width:@desktop-size) {
        bottom: -200px;
        left: 0;
        right: 0;
        margin: 0 auto;
        width: 264px;
        padding: 80px 12px 20px;
    }
    &::after {
        position: absolute;
        content: "";
        width: 66px;
        height: 73px;
        top: 15px;
        right: -20px;
        background: url("../img/microscope-icon.svg");
        background-repeat: no-repeat;
        background-size: 66px 73px;
        background-clip: padding-box;
        opacity: 0.3;
        transform: rotate(15deg);
        @media (min-width:@desktop-size) {
            transform: rotate(0deg);
            background-size: 36px 40px;
            opacity: 1;
            top: 20px;
            left: 0;
            right: 0;
            margin: 0 auto;
            
            width: 48px;
            height: 48px;
            
        }
    }


    // background-image: url("../img/microscope-icon.svg");
    // background-position: bottom right;
    // background-size: 66px 73px;
    // background-repeat: no-repeat;
    // background-clip: padding-box;
}

.intro__advantage-list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.intro__advantage-item {
    .font-style(14px; 20px; 400);
    text-align: center;
}

.intro__advantage-toggle {
    width: 40px;
    height: 3px;
    border: none;
    border-radius: 2px;
    background-color: @grey;
}
