.footer {
    background-image: url("../img/footer-texture.png");
    background-color: @dark-grey;
    padding-top: 38px;
    padding-bottom: 38px;
    color: @white;
    @media (min-width:@desktop-size) {
        padding-top: 70px;
        padding-bottom: 32px;
    }
}

.footer-section {
    @media (min-width:@tablet-size) {
        display: grid;
        grid-template-columns: auto auto;
        column-gap: 80px;
        row-gap: 24px;
    }
    @media (min-width:@desktop-size) {
        grid-template-columns: auto auto auto;
        gap: 20px;
        margin-bottom: 88px;
    }
}

.footer-section__contacts {
    @media (min-width:@desktop-size) {
        max-width: 315px;
        margin-right: 110px;
    }
}

.footer-section__branch {
    @media (min-width:@desktop-size) {
        position: relative;
    }
}

.footer-section__logo-link {
    display: block;
    width: 160px;
    height: 57px;
    margin-bottom: 20px;
    @media (min-width:@desktop-size) {
        margin-bottom: 32px;
    }
}

.footer-section__phone {
    text-decoration: none;
    color: @white;
    .font-style(20px; 24px; 700);
    margin: 0;
    margin-bottom: 16px;
    display: block;
    @media (min-width:@desktop-size) {
        display: none;
    }
}

.footer-section__adds {
    .font-style(14px; 21px; 400);
    margin: 0;
    padding-bottom: 32px;
    @media (min-width:@desktop-size) {
        .font-style(15px; 23px; 400);
        padding-bottom: 36px;
    }

}

.footer-section__adds-city {
    margin: 0;
    padding-bottom: 8px;
}

.footer-section__adds-list {
    list-style: none;
    margin: 0;
    padding-left: 24px;
    padding-bottom: 8px;
}

.footer-section__adds-item {
    position: relative;
    margin-bottom: 4px;
    &::before {
        position: absolute;
        content: "";
        width: 16px;
        height: 16px;
        top: 2px;
        left: -24px;
        background-image: url(../img/list-point-footer-icon.svg);
    }
}

.footer-section__adds-button {
    .font-style(12px; 18px; 400);
    margin: 0;
    padding-bottom: 20px;
}

.footer-section__adds-mode {
    .font-style(14px; 21px; 400);
    margin: 0;
    @media (min-width:@desktop-size) {
        .font-style(15px; 23px; 400);
    }
}

.footer-section__links {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;
    gap: 26px;
}

.footer-section__social-list {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 13px;
}

.footer-section__buttons {
    display: flex;
    flex-direction: column;
    gap: 12px;
    padding-top: 24px;
    padding-bottom: 32px;
}

.footer-section__nav {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-bottom: 24px;
    @media (min-width:@desktop-size) {
        padding: 0;
        gap: 20px;
    }
}

.footer-section__list {
    list-style: none;
    margin: 0;
    padding: 0;
}

.footer-section__menu-item {
    .font-style(15px; 23px; 400);
    padding: 10px 24px 10px 0;
    margin: 0;
}

.footer-section__menu-link {
    text-decoration: none;
    .font-style(15px; 23px; 400);
    color: @white;
    margin: 0;
    padding: 0;
}

.footer-section__specialists-item {
    padding: 7px 24px 7px 0;
    margin: 0;
    @media (min-width:@desktop-size) {
        padding: 9px 24px 9px 0;
    }
}

.footer-section__specialists-link {
    text-decoration: none;
    .font-style(12px; 18px; 400);
    color: @white;
    margin: 0;
    padding: 0;
}

.footer-section__copyright {
    margin-bottom: 20px;
    @media (min-width:@tablet-size) {
        grid-column-start: 1;
        grid-column-end: 3;
    }
    @media (min-width:@desktop-size) {
        max-width: 425px;
        grid-column-start: auto;
        grid-column-end: auto;
        margin-bottom: 0;
    }
}

// .footer-section__aside {
//     @media (min-width:@desktop-size) {
//         margin-bottom: 100px;
//     }
// }

.footer-section__aside-phone {
    display: none;
    @media (min-width:@desktop-size) {
        display: block;
        text-decoration: none;
        color: @white;
        .font-style(28px; 34px; 700);
        margin: 0;
        margin-bottom: 20px;
    }
}

.footer-section__aside-content {
    .font-style(12px; 17px; 400);
    color: @footer-grey;
    margin: 0;
    padding-bottom: 12px;
    @media (min-width:@desktop-size) {
        padding-bottom: 16px;
    }
}

.footer-section__aside-image {
    margin-bottom: 24px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 12px;
    justify-content: space-around;
    @media (min-width:@desktop-size) {
        flex-wrap: nowrap;
        margin-bottom: 104px;
    }
}

.footer-section__aside-link {
    display: block;
    width: 154px;
    height: 47px;
}

// .footer-info {

// }

.footer-info__list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    &--doc {
        gap: 8px;
        padding-bottom: 32px;
        border-bottom: 1px solid @footer-extra-grey; 
        border-radius: 1px;
        @media (min-width:@tablet-size) {
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-between;
            align-items: baseline
        }
        @media (min-width:@desktop-size) {
            padding-bottom: 20px;
        }
    }
    &--info {
        gap: 16px;
        padding-top: 32px;
        @media (min-width:@desktop-size) {
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: space-between;
            align-items: baseline;
        }
    }
}

.footer-info__item-doc {
    padding: 0;
    margin: 0;
    .font-style(12px; 18px; 400);
}

.footer-info__link {
    text-decoration: none;
    color: @footer-grey;
}

.footer-info__item-info {
    padding: 0;
    margin: 0;
    .font-style(10px; 15px; 400);
    color: @footer-grey;
}

.footer-info__link--dev {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 5px;
}

.footer-info__link--dev-img {
    width: 68px;
    height: 23px;
}
