.aid-wrapper {
    padding-bottom: 80px;
    @media (min-width:@desktop-size) {
        padding-bottom: 120px;
    }
}

.aid__title {
    .font-style(24px; 29px; 700);
    margin: 0;
    margin-bottom: 8px;
    text-align: center;
    @media (min-width:@desktop-size) {
        .font-style(48px; 58px; 700);
    }
}

.aid__text {
    .font-style(15px; 23px; 400);
    text-align: center;
    margin: 0 20px 20px;
    @media (min-width:@desktop-size) {
        .font-style(18px; 27px; 400);
    }
}

.aid__filter-list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex; 
    flex-direction: row;
    overflow-x: scroll;
    margin-bottom: 12px;
    @media (min-width:@desktop-size) {
        margin-bottom: 24px;
    }
}

.aid__filter-item {
    .font-style(14px; 21px; 400);
    padding: 11px 24px;
    &--current {
        background-color: @grey;
        border-radius: 8px;
    }
    @media (min-width:@desktop-size) {
        .font-style(15px; 23px; 400);
    }
}

.aid__options {
    background: repeating-linear-gradient(
  280deg,
  @light-grey,
  @light-grey 160px,
  fade(@white, 50%) 160px,
  fade(@white, 50%) 320px
);
    // background: repeating-linear-gradient (290deg, fade(@white, 50%), fade(@white, 50%) 160px, @light-grey 160px, @light-grey 320px);
    // background-image: url("../img/bg-print-2.svg");

    // background-repeat: no-repeat;
    // background-size: cover;
    // background-position: 80% 97%;
    background-color: @light-grey;
    border-radius: 16px;
    padding-top: 16px;
    padding-bottom: 24px;
    padding-left: 16px;
    padding-right: 16px;
    position: relative;
    @media (min-width:@desktop-size) {
        padding-bottom: 40px;
        padding-left: 24px;
        padding-right: 24px;
    }
}

.aid__options-title {
    .font-style(20px; 26px; 700);
    .title--color {
        color: @turquoise;
    }
    @media (min-width:@desktop-size) {
        .font-style(28px; 36px; 700);
        width: 50%;
    }
}

.aid__options-type-list {
    list-style: none;
    padding: 4px;
    margin: 0;
    margin-bottom: 16px;
    border: 1px solid @grey;
    border-radius: 8px;
    display: flex;
    justify-content: space-between;
    background-color: @white;
    @media (min-width:@desktop-size) {
        position: absolute;
        top: 24px;
        right: 24px;
        width: max-content;
    }
}

.aid__options-type-item {
    .font-style(12px; 14px; 400);
    padding: 11px 16px;
    text-align: center;
    width: 50%;
    &--current {
        background-color: @grey;
        border-radius: 6px;
    }
    @media (min-width:@desktop-size) {
        width: max-content;
    }
}

.aid__options-specialists-list {
    list-style: none;
    padding: 0 24px;
    margin: 0;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    @media (min-width: @tablet-size) {
        max-height: 450px;
    }
    @media (min-width:@desktop-size) {
        max-height: 250px;
    }
}

.aid__options-specialists-item {
    .font-style(14px; 21px; 400);
    margin-bottom: 4px;
    max-width: max-content;
    @media (min-width:@desktop-size) {
        .font-style(15px; 20px; 400);
        width: 20%;
    }
}
