.promo {
    padding-bottom: 80px;
    @media (min-width:@desktop-size) {
        padding-bottom: 120px;
    }
}

.promo-wrapper {
    // background-color: @light-grey;
    border: 2px solid @light-turquoise;
    border-radius: 16px;
    padding: 6px;
    // padding-top: 22px;
    @media (min-width:@desktop-size) {
        display: flex;
        flex-direction: row;
        gap: 70px;
    }
}

.promo__block--text {
    padding-top: 16px;
    padding-left: 8px;
    padding-right: 8px;
    @media (min-width:@desktop-size) {
        padding-left: 16px;
        padding-right: 16px;
    }
}

.promo__title {
    .font-style(20px; 24px; 700);
    margin: 0;
    padding-bottom: 8px;
    .title--color {
        color: @turquoise;
    }
    @media (min-width:@desktop-size) {
        .font-style(28px; 34px; 700);
    }
}

.promo__text {
    .font-style(14px; 21px; 400);
    margin: 0;
    padding-bottom: 16px;
    @media (min-width:@desktop-size) {
        .font-style(15px; 23px; 400);
    }
}

.promo__questions {
    background-color: @light-grey;
    border-radius: 16px;
    padding: 20px 16px;
    @media (min-width:@desktop-size) {
        padding: 24px;
        padding-bottom: 9px;
    }
}

.promo__questions-block {
    margin-bottom: 16px;
}

.promo__questions-number {
    .font-style(12px; 14px; 400);
    color: @text-grey;
    margin: 0;
    padding-bottom: 4px;
}

.promo__questions-title {
    .font-style(16px; 24px; 400);
    margin: 0;
    padding-bottom: 8px;
    @media (min-width:@desktop-size) {
        .font-style(18px; 27px; 400);
    }
}

.promo__questions-list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 8px;
}

.promo__questions-item {
    .font-style(12px; 14px; 400);
    padding: 11px 16px;
    background-color: @white;
    border-radius: 6px;
    position: relative;
    &--current {
        background-color: @light-turquoise;
        padding-left: 32px;
        &::after {
            position: absolute;
            content: "";
            width: 8px;
            height: 8px;
            border-radius: 8px;
            background-color: @turquoise;
            top: 14px;
            left: 16px;
        }
    }
    @media (min-width:@desktop-size) {
        .font-style(16px; 21px; 400);
        padding: 11px 24px;
        &--current {
            padding-left: 32px;
            padding-right: 16px;
            &::after {
                top: 17px;
                left: 16px;
            }
        }
    }
}

.promo__questions-button {
    @media (min-width:@desktop-size) {
        display: none;
    }
}
