.certificates {
    padding-bottom: 80px;
    @media (min-width:@desktop-size) {
        padding-bottom: 120px;
    }
}

.certificates__title {
    .font-style(24px; 29px; 700);
    text-align: center;
    margin: 0;
    padding-bottom: 20px;
    @media (min-width:@desktop-size) {
        .font-style(48px; 58px; 700);
        padding-bottom: 32px;
    }
}

.certificates__list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex; 
    flex-direction: row;
    justify-content: space-between;
    gap: 12px;
    overflow-x: scroll;
    // box-sizing: border-box;
    @media (min-width:@desktop-size) {
        gap: 20px;
        flex-wrap: wrap;
        overflow: hidden;
    }
}

.certificates__item {
    border-radius: 12px;
    min-width: 280px;
    @media (min-width:@desktop-size) {
        width: 315px;
        position: relative;
        // margin-bottom: 5px;
        box-sizing: border-box;
        &::after{
            position: absolute;
            content: "";
            width: 100%;
            height: 100%;
            background: @white;
            top: 0;
            left: 0;
            border: 4px solid @grey;
            border-radius: 12px;
        }
        
    }
}

.certificates__item-image {
    border-radius: 12px;
}

.certificates__item-title {
    display: none;
    @media (min-width:@desktop-size) {
        display: block;
    }

}

.certificates__item-text {
    @media (min-width:@desktop-size) {
        display: block;
    }
}

.certificates__item-data {
    @media (min-width:@desktop-size) {
        display: block;
    }
}

