.header-menu-wrapper {
    background-color: @white;
    padding: 14px 0;
    min-height: 72px;
    box-sizing: border-box;
    @media (min-width:@desktop-size) {
        // min-height: 90px;
        padding-top: 23px;
    }
}

.header-menu {
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column;
    @media (min-width:@desktop-size) {
        flex-direction: row;
        align-items: flex-start;
        justify-content: space-between;
        gap: 20px;
        min-height: 90px;
        // flex-wrap: nowrap;
        // box-sizing: border-box;
    }
}

.main-nav {
    display: none;
    width: 100%;
    @media (min-width:@desktop-size) {
        display: block;
        padding: 0;
        margin: 0;
        // box-sizing: border-box;
    }
}

.main-nav__wrapper {
    @media (min-width:@desktop-size) {
        display: grid;
        grid-template-columns: 1fr auto;
        gap: 50px;
        // box-sizing: border-box;
    }
}

.main-nav__toggle {
    position: absolute;
    top: 0;
    left: 0;
    width: 44px;
    height: 44px;
    background-color: transparent;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    &::before, &::after {
        position: absolute;
        content: "";
        width: 16px;
        height: 1px;
        left: 14px;
        border-radius: 1px;
        background-color: @special-black;
    }
    &::before {
        top: 16px;
    }
    &::after {
        bottom: 16px;
    }
    // @media (min-width:@tablet-size) {
        // top: 40px;
    // }
    @media (min-width:@desktop-size) {
        display: none;
    }
}

.main-nav__list {
    list-style: none;
    padding: 0;
    margin: 0;
//     display: flex;
//     flex-direction: column;
//     width: 100vw;
    @media (min-width:@desktop-size) {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }
}

.main-nav__item {
    margin: 0;
    padding: 0;
}

.main-nav__item-link {
    .font-style(15px; 20px; 400);
    color: @special-black;    
    text-decoration: none;
    width: 100%;
    text-align: left;
    position: relative;
    left: 0;
    top: 10px;
    padding: 13px 0;
    @media (min-width:@desktop-size) {
        padding: 13px 8px;
    }
}

.appointment {
    display: flex;
    flex-direction: column;
    gap: 20px;
    // flex-wrap: nowrap;
    @media (min-width:@desktop-size) {
        flex-direction: row;
        gap: 16px;
        // flex-wrap: wrap;
        // box-sizing: border-box;
        min-width: min-content;
        // height: min-content;
    }
}

.appointment__phone {
    .font-style(18px; 23px; 500);
    text-decoration: none;
    color: @turquoise;
}

.appointment__work-hour {
    .font-style(12px; 16px; 400);
    margin: 0;
}

.appointment__form {
    display: flex;
    gap: 12px;
    @media (min-width:@desktop-size) {
        gap: 8px;
        // box-sizing: border-box;
    }
}

.appointment__form-user {
    width: 44px;
    height: 44px;
    border: none;
    border-radius: 8px;
    background-color: @grey;
    background-image: url("../img/icon-user.svg");
    background-repeat: no-repeat;
    background-position: 50% 50%;
    padding: 0;
    flex-shrink: 0;
}

.main-nav__call {
    position: absolute;
    content: "";
    top: 0;
    right: 0;
    width: 44px;
    height: 44px;
    background-color: @turquoise;
    background-image: url("../img/icon-call.svg");
    background-repeat: no-repeat;
    background-position: 50% 50%;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    @media (min-width:@desktop-size) {
        display: none;
    }
}

.header-menu-wrapper--close {
    background-color: fade(#000000, 30%);
    .main-nav__toggle {
        width: 50px;
        &::before {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            height: 4px;
            width: 50px;
            border-radius: 3px;
            @media (min-width:@tablet-size) {
                top: 10px;
            }
            // @media (min-width:@desktop-size) {
            //     padding-top: 15px;
            // }
        }
    }
    .main-nav__wrapper {
        display: none;
        @media (min-width:@desktop-size) {
            display: block;
        }
    }
}

