.specialist {
    background-color: @bg-grey;
    padding: 40px 0;
    @media (min-width:@desktop-size) {
        padding: 60px 0;
    }
}

.specialist__title {
    .font-style(24px; 29px; 700);
    text-align: center;
    margin: 0;
    padding-bottom: 20px;
    @media (min-width:@desktop-size) {
        .font-style(48px; 58px; 700);
        padding-bottom: 32px;
    }
}

.specialist__filter {
    // position: relative;
    @media (min-width: @tablet-size) {
        display: flex;
        gap: 16px;
    }
}

.specialist__filter-list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex; 
    flex-direction: row;
    overflow-x: scroll;
    margin-bottom: 12px;
    @media (min-width: @tablet-size) {
        gap: 12px;
    }
    @media (min-width:@desktop-size) {
        margin-bottom: 28px;
    }
}

.specialist__filter-item {
    .font-style(14px; 21px; 400);
    padding: 11px 24px;
    &--current {
        background-color: @grey;
        border-radius: 8px;
    }
    @media (min-width:@desktop-size) {
        .font-style(15px; 23px; 400);
    }
}

.specialist__filter-button {
    display: none;
    @media (min-width: @tablet-size) {
        display: flex;
        gap: 8px;
        // position: relative;
        // top: 0;
        // right: 0;
        // max-width: min-content;
    }
}

.specialist__button-switch {
    @media (min-width: @tablet-size) {
        width: 44px;
        height: 44px;
        border-radius: 8px;
        border: none;
        background-color: @grey;
        position: relative;
        &--prev::before, &--next::before {
            position: absolute;
            content: "";
            top: 16px;
            width: 9px;
            height: 9px;
            border-top: 1px solid @special-black;
            border-right: 1px solid @special-black;
            border-radius: 1px;
        }
        &--prev::before {
            left: 19px;
            transform: rotate(-135deg);
            
        }
        &--next::before {
            right: 19px;
            transform: rotate(45deg);
            
        }
    }
}

.specialist__list {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: row;
    gap: 12px;
    margin-bottom: 20px;
    overflow-x: scroll;
    @media (min-width:@desktop-size) {
        gap: 20px;
        margin-bottom: 28px;
    }
}

.specialist__item {
    background-color: @white;
    border-radius: 12px;
    min-width: 280px;
    padding: 8px;
    padding-bottom: 16px;
    // box-sizing: border-box;
    @media (min-width:@desktop-size) {
        width: 315px;
        padding-bottom: 24px;
    }
}

.specialist__item-info {
    background-image: url("../img/bg-print.svg");
    background-repeat: no-repeat;
    background-position: 50% 90%;
    background-color: @special-turquoise;
    // width: 264px;
    // height: 292px;
    border-radius: 12px;
    position: relative;
    display: flex;
    justify-content: end;
    margin-bottom: 16px;
    @media (min-width:@desktop-size) {
        margin-bottom: 24px;
    }
}

.specialist__image {
    object-fit: contain;
    border-radius: 12px;
}

.specialist__item-info-point {
    .font-style(12px; 18px; 400);
    background-color: @white;
    border-radius: 6px;
    padding: 6px 10px;
    margin: 0;
}

.specialist__direction {
    position: absolute;
    top: 8px;
    left: 8px;
}

.specialist__experience {
    position: absolute;
    top: 42px;
    left: 8px;
    color: @text-grey;
    &--years {
        color: @special-black;
    }
}

.specialist__recommendation {
    position: absolute;
    bottom: 8px;
    left: 8px;
    color: @extra-grey;
    background-color: @light-turquoise;
    &--percent {
        color: @special-black;
    }
    @media (min-width:@desktop-size) {
        padding: 10px;
    }
}

.specialist__name {
    .font-style(16px; 24px; 400);
    margin: 0;
    padding-bottom: 4px;
    @media (min-width:@desktop-size) {
        .font-style(18px; 27px; 400);
    }
}

.specialist__status {
    .font-style(14px; 21px; 400);
    margin: 0;
    padding-bottom: 12px;
    @media (min-width:@desktop-size) {
        .font-style(15px; 23px; 400);
        padding-bottom: 16px;
    }
}
